import React, { Component } from "react";
import joi from "joi";
import { Redirect } from "react-router-dom";
const axios = require("axios");
const Validation = e => {
  e.preventDefault();
  const { name, value } = e.target;
  const schema = {
    FirstName: joi.string().min(1),
    LastName: joi.string().min(1),
    Email: joi
      .string()
      .email({ minDomainSegments: 2, tlds: { allow: ["com", "net"] } })
  };
  const { error } = joi.validate({ [name]: value }, schema);
  let ErrorMsg = document.getElementById("Error" + name);
  if (error) ErrorMsg.innerHTML = error.details[0].message;
  else ErrorMsg.innerHTML = "";
};

const OnSubmit = e => {
  e.preventDefault();
  const errors = document.querySelectorAll(".errorMsg");
  let flag = false;
  errors.forEach(element => {
    if (element.innerHTML) flag = true;
  });
  if (flag) {
    alert("Please Check your Errors");
  } else {
    //.....
  }
};

class SignUpJobSeeker extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Email: "",
      password: "",
      FirstName: "",
      LastName: "",
      repass: "",
      userName: "",
      errors: []
    };
  }
  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleSubmit = () => {
    if (this.state.password === this.state.repass) {
      axios
        .post("http://localhost:8000/api/customersignup", this.state)
        .then(res => {
          if (res.status == 200) {
            alert(res.data.success);
          }
        })
        .catch(e => this.state.errors.push(e));
    }
  };
  render() {
    return (
      <div className="container">
        <div className="col-6 form">
          <form onSubmit={OnSubmit}>
            <div className="form-row mt-5">
              <div className="col">
                <input
                  name="FirstName"
                  type="text"
                  className="form-control"
                  placeholder="First name"
                  onBlur={Validation}
                  onChange={this.handleChange.bind(this)}
                  aria-describedby="ErrorFirstName"
                />
                <small
                  id="ErrorFirstName"
                  className="form-text errorMsg"
                ></small>
              </div>
              <div className="col">
                <input
                  name="LastName"
                  type="text"
                  className="form-control"
                  placeholder="Last name"
                  onBlur={Validation}
                  aria-describedby="ErrorLastName"
                  onChange={this.handleChange.bind(this)}
                />
                <small
                  id="ErrorLastName"
                  className="form-text errorMsg"
                ></small>
              </div>
            </div>
            <div className="form-group mt-4">
              <input
                name="Email"
                type="text"
                className="form-control"
                id="inputAddress"
                placeholder="Email"
                onBlur={Validation}
                onChange={this.handleChange.bind(this)}
                aria-describedby="ErrorEmail"
              />
              <small id="ErrorEmail" className="form-text errorMsg"></small>
              <img src={"Image/Icons/mail.png"} alt="" width="30" />
            </div>
            <div className="form-group">
              <input
                type="text"
                name="userName"
                className="form-control"
                id="inputAddress"
                onChange={this.handleChange.bind(this)}
                placeholder="User Name"
              />
              <img src={"Image/Icons/person.png"} alt="" width="30" />
            </div>
            <div className="form-group">
              <input
                type="password"
                name="password"
                className="form-control"
                id="inputAddress"
                onChange={this.handleChange.bind(this)}
                placeholder="Password"
              />
              <img src={"Image/Icons/key.png"} alt="" width="30" />
            </div>
            <div className="form-group">
              <input
                type="password"
                name="repass"
                className="form-control"
                id="inputAddress"
                onChange={this.handleChange.bind(this)}
                placeholder="Confirm Password"
              />
              <img src={"Image/Icons/lock.png"} alt="" width="30" />
            </div>
            <div className="form-group form-check mt-5 checkbox">
              <input
                type="checkbox"
                className="form-check-input"
                id="exampleCheck1"
              />
              <label className="form-check-label" htmlFor="exampleCheck1">
                <a href="#">Accept terms and conditions</a>
              </label>
            </div>
            <button
              type="button"
              className="btn btn-secondary btn-lg btn-block btn-register"
              onClick={this.handleSubmit}
            >
              Register
            </button>
          </form>
        </div>
        <div className="SigninApis">
          <div className="google">
            <img src={"Image/Icons/google.png"} alt="" width="50" height="50" />
            <img
              src={"Image/orange.png"}
              className="shape"
              alt=""
              width="280"
            />
            <h6>Sign in with Google </h6>
          </div>
          <div className="facebook">
            <img
              src={"Image/Icons/facebook.png"}
              alt=""
              width="50"
              height="50"
            />
            <img src={"Image/blue.png"} className="shape" alt="" width="280" />
            <h6>Sign in with Facebook </h6>
          </div>
          <div className="linkedin">
            <img
              src={"Image/Icons/linkedin.png"}
              alt=""
              width="50"
              height="50"
            />
            <img
              src={"Image/orange.png"}
              className="shape"
              alt=""
              width="280"
            />
            <h6>Sign in with LinkedIn </h6>
          </div>
          <div className="twitter">
            <img
              src={"Image/Icons/twitter.png"}
              alt=""
              width="50"
              height="50"
            />
            <img src={"Image/blue.png"} className="shape" alt="" width="280" />
            <h6>Sign in with Twitter </h6>
          </div>
        </div>
        <button type="button" onClick={this.handleSubmit}>
          try
        </button>
      </div>
    );
  }
}

export default SignUpJobSeeker;
