import React, { Component } from "react";
const axios = require("axios");
class SignUpHotel extends Component {
  state = {
    errors: [],
    data: {
      name: "",
      user_name: "",
      password: "",
      repassword: "",
      document_link: "D:/Accadamics/5th 1st semester/Mini Project",
      email: "",
      location: "",
      phone: "",
      contact_person: ""
    }
  };
  handleClick = () => {
    console.log("called");
    axios
      .post("http://localhost:8000/api/hotelSignup", this.state.data)
      .then(res => console.log(res.status))
      .catch(e => this.state.errors.push(e));
  };

  handleChange = event => {
    let data = this.state.data;
    data[event.target.name] = event.target.value;
    this.setState({ data: data });
  };
  render() {
    return (
      <div className="container Cform">
        <div className="row my-5">
          <div className="col-6">
            <form>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  name="name"
                  onChange={this.handleChange.bind(this)}
                  placeholder="Hotel Name"
                />
                <img src={"Image/Icons/building.png"} alt="" width="30" />
              </div>
              <div className="form-group mt-4">
                <input
                  type="text"
                  name="email"
                  className="form-control"
                  id="inputAddress"
                  placeholder="Email"
                  onChange={this.handleChange.bind(this)}
                />
                <img src={"Image/Icons/mail.png"} alt="" width="30" />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="location"
                  className="form-control"
                  onChange={this.handleChange.bind(this)}
                  id="inputAddress"
                  placeholder="Location"
                />
                <img src={"Image/Icons/location.png"} alt="" width="30" />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  id="inputAddress"
                  name="phone"
                  onChange={this.handleChange.bind(this)}
                  placeholder="Phone Number"
                />
                <img src={"Image/Icons/phone.png"} alt="" width="30" />
              </div>
            </form>
            <div className="row asset-container h-50">
              <div className="asset h-100">
                <img
                  src={"Image/Icons/photos.png"}
                  alt=""
                  width="70"
                  height="70"
                />
              </div>
              <div className="col-3 ml-5 mt-5">
                <h6 className="w-100 ml-2">
                  Please Provide Your Legal License Document
                </h6>
                <input type="file" name="document_link" />
              </div>
            </div>
          </div>
          <div className="col-6 ">
            <form>
              <div className="form-group">
                <input
                  type="text"
                  name="contact_person"
                  className="form-control"
                  onChange={this.handleChange.bind(this)}
                  placeholder="Owner Name"
                />
              </div>
              <div className="form-group mt-4">
                <h2>Account</h2>
              </div>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  id="inputAddress"
                  name="user_name"
                  onChange={this.handleChange.bind(this)}
                  placeholder="User Name"
                />
                <img src={"Image/Icons/person.png"} alt="" width="30" />
              </div>
              <div className="form-group">
                <input
                  type="password"
                  name="password"
                  className="form-control"
                  id="inputAddress"
                  onChange={this.handleChange.bind(this)}
                  placeholder="Password"
                />
                <img src={"Image/Icons/key.png"} alt="" width="30" />
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className="form-control"
                  name="repassword"
                  onChange={this.handleChange.bind(this)}
                  id="inputAddress"
                  placeholder="Confirm Password"
                />
                <img src={"Image/Icons/lock.png"} alt="" width="30" />
              </div>
              <div class="form-group form-check mt-5 checkbox">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="exampleCheck1"
                />
                <label class="form-check-label" for="exampleCheck1">
                  <a href="#">Accept terms and conditions</a>
                </label>
              </div>
              <button
                type="button"
                onClick={this.handleClick}
                class="btn btn-secondary btn-lg btn-block Cbtn-register"
              >
                Register
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default SignUpHotel;
